/* App.css */
@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

* {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}
.navbar-grid {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  background: #121212;
  color: white;
  padding: 16px 16.5px 5px;
  position: fixed;
  width: 100%;
  z-index: 9999;
  top: -8px;
  box-sizing: border-box;
}
.nav-left{
  text-align: left;
  font-size: 20px;
  margin-left: 10px;
}
.nav-right {
  text-align: right;
  font-size: 17px;
}
.nav-center {
  text-align: center;
  font-size: 1rem;
  font-weight: 200;
}
.nav-right a {
  text-decoration: none;
  color: #ffffff;
  font-weight: bold;
  margin: 0 20px 0 0;
}



:root{
  --my-blue: #026bde;
  --default-bg: #121212;
  }
.card-holder .event-sellers {
background: black;
border-radius: 10px 10px 0 0px;
padding: 8px;
background-color: var(--my-blue);
color: #fff;
}
.seat-dets {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 100%;
  color: #ffff;
  background: var(--my-blue);
  height: 6.6rem;
  align-items: center;
  padding: 10px;
}
.seat-det-sent {
  background: rgb(10, 110, 229);
}
.seat-dets p:nth-child(1),.seat-dets p:nth-child(2), .seat-dets p:nth-child(3) {
transform: translateY(5px);
font-weight: lighter;
font-size: 13.5px;
}
.ga div p:nth-child(1) {
  transform: translateY(5px);
}
.ga div p:nth-child(2), .ga p:nth-child(2) {
  color: #fff;
  font-size: 1.15rem;
  font-weight: bolder;
  margin-top: 7px;
}
.ga div p:nth-child(2) {
  transform: translateY(0px);
}
.seat-dets p:nth-child(4),.seat-dets p:nth-child(5), .seat-dets p:nth-child(6) {
  color: #fff;
  transform: translateY(-10px);
  font-size: 22px;
  font-weight: bolder;
}
.seat-det-sent {
  background-color: rgb(76, 76, 85,.9);
}
.card-img {
width: 100%;
aspect-ratio: 16/9;
}
.info-field, .info-sent {
height: 12rem;
background: linear-gradient(transparent, transparent,  rgb(0, 0, 0, .5), black);
margin-top: -12rem;
position: relative;
padding: 10px;
}
.info-sent {
  background: linear-gradient(transparent, rgb(0, 0, 0, .2),  rgb(0, 0, 0, .8), black);
}
.info-field p, .info-sent p {
  transform: translateY(7.5rem);
  color: #fff;
}
.info-field p {
  transform: translateY(6rem);
}
.info-sent p  {
  transform: translateY(4.5rem);
}
.info-field p:nth-child(1), .info-sent .title-sent {
  font-weight: 650;
  font-size: 17.3px;
  display: flex;
  height: 3.5rem;
  align-items: flex-end;
  justify-content: center;
}
.ga {
  display: flex;
  justify-content: space-around;
  column-gap: 25px;
  align-items: center;
}
.ga div, .ga p:nth-child(2) {
  transform: translateY(-10px);
}
.info-field p:nth-child(2), .info-sent .sent-time {
  font-size: 13px;
  width: 100%;
  display: flex;
  align-items: center;
  height: 1.6rem;
  line-height: 14px;
  justify-content: center;
}
.entry {
  margin-top: 20px;
  letter-spacing: -0.5px;
  font-size: 14px;
}
.walletimg {
  width: 15rem;
  margin: 20px auto 10px auto;
}
.sections {
  color: var(--my-blue);
  display: flex;
  justify-content: space-around;
  text-align: center;
  margin: 20px 0;
}
.dets {
  transform: translateY(15px);
  color: var(--my-blue);
  padding: 5px 0 20px 0;
}
.bottom, .bottom-sent {
  width: 100%;
  height: 2rem;
  border-radius: 0 0 10px 10px;
  transform: translateY(25px);
}
.bottom {
  background-color: var(--my-blue);
}
.bottom-sent {
  background: #4c4c55;
}
.btn {
  text-align: center;
}
.btn button {
  width: 10rem;
  margin: 10px 10px 20px;
  padding: 12px;
  border-radius: 5px;
  border: none;
  color: #fff;
  font-size: 14px;
  font-weight: 501;
}
.btn button:nth-child(1) {
  background: var(--my-blue);
}
.btn button:nth-child(2) {
  background-color: rgb(128, 128, 128, .5);
}

img{
  width: 100%;
  image-rendering: crisp-edges;
}
.bottompic{
  width: 100%;
  z-index: 100;
}
.transfer-form {
  position: fixed;
  bottom: 0;
  height: 10px;
  width: 100%;
  z-index: 999;
  border-radius: 3px 3px 0;
  text-align: center;
  animation: animate .2s normal;
  transition: transform 2s ease-in-out;
}
@keyframes animate {
  10% {
    position: absolute;
    right: -10rem;
  }
  25% {
    bottom: -5rem;
  } 100% {
      right: 0;
  }
}
.submit {
  position: absolute;
  bottom: 50px;
  right: 20px;
  width: 10rem;
  padding: 0 15px;
  height: 2.7rem;
  background: var(--my-blue);
  border-radius: 8px;
  border: none;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}
.submit a {
  color: #fff;
  font-weight: 100;
  text-transform: capitalize;
  text-decoration: none;
  
}
.nav-left-btn {
  position: absolute;
  bottom: 60px;
  left: 20px;
  font-size: 20px;
  color: var(--my-blue);
  display: flex;
  align-items: center;
  gap: 7px;
  font-weight: 400;
}
.transfer-form input[type="text"], textarea {
  padding: 7px 5px;
  outline: none;
  width: 90%;
  border-radius: 2px;
  border: 1.5px solid rgb(0, 0, 0, .5);
  margin-top: 20px;
  resize: none;
  transform: translateY(-38px);
  font-weight: 500;
  margin-bottom: -2px;
  font-size: 18px;
  transition: all 400ms ease;
}
.transfer-form input[type="radio"] {
  position: absolute;
  transform: translate(-30px, 0px);
}

.transfer-form input[type="text"]::-webkit-input-placeholder, .transfer-form textarea::-webkit-input-placeholder {
  color: rgb(128, 128, 128, .5);
  font-size: .9rem;
  transform: translateX(4px);
}
.transfer-form textarea::-webkit-input-placeholder {
  height: 100%;
  transform: translate(16rem, 60px);
}
.transfer-form input[type="text"]:nth-child(11) {
transform: translateY(-20px);
}
.transfer-form input[type="text"]:nth-child(15) {
  transform: translateY(-30px);
}
.transfer-form textarea {
  transform: translateY(-38px);
  height: 5.4rem;
}
.transfer-form input[type="submit"] {
  transform: translateY(10px);
}

.transfer-form label {
  position: absolute;
  left: 20px;
  font-size: 15px;
  transform: translateY(-20px);
  color: rgb(0, 0, 0, .77);
}

.transfer-form label:nth-child(5) {
transform: translateY(-7px);
}
.transfer-form label:nth-child(9) {
  transform: translateY(-7.5px);
  }
  .transfer-form label:nth-child(13) {
    transform: translateY(-18.5px);
    }
    .transfer-form label:nth-child(17) {
      transform: translateY(-18.5px);
      }
.transfer-form h3, .selectee {
  transform: translateY(6px);
  font-weight: 100;
  font-size: 15px;
  margin: 5px 0;
  color: rgb(0,0,0, .8);
}
.transfer-form hr {
  margin: 20px 0;
  transform: translateY(-4px);
}
.transfer-form .selected-tix {
font-weight: 400;
transform: translate(20px, -10px);
}
.transfer-form p:not(.selected-tix) {
  margin: 10px 0;
}
.transfer-form p:not(.selected-tix) span {
  -webkit-text-stroke: .35px;
}
.transfer-form p {
  text-align: left;
  transform: translateX(20px) translateY(-10px);
}
.swipe-area {
  height: 10px;
  width: 100px;
  background-color: rgb(128, 128, 128, .4);
  margin: 0 auto;
  border-radius: 20px;
  transform: translateY(-3px);
}
.blur {
  backdrop-filter: blur(5px);

}
.cover {
  height: 14rem;
  background: linear-gradient(to bottom, rgb(0, 0, 0, .1), rgb(0, 0, 0, .4), rgb(0, 0, 0, .5), rgb(0, 0, 0));
  width: 95.4%;
  color: #ffff;
  margin: 0 auto 0 auto;
  position: absolute;
  bottom: 0;
  transform: translateX(.62rem);
}
.show-btn img {
  margin-top: 50px;
  width: 100%;
  scale: .95;
  object-fit: contain;
  outline: none;
}
.show-btn {
  text-align: center;
  margin-top: 10px;
  margin-bottom: -3.5rem;
  overflow: hidden;
  animation: blip 700ms normal;
  transition: animation 2s ease;
  height: 17.5rem;
  position: relative;
}
@keyframes blip {
  10% {
    opacity: 0.8;
    scale: 0.9;
  } 100% {
    opacity: 1;
    scale: 1;
  }
}

.cover p {
  text-align: left;
  transform: translate(10px, 0);
}
.cover p:nth-child(1):not(.cover span p) {
  font-weight: 500.5;
  font-size: 21px;
  height: 2rem;
  display: flex;
  align-items: flex-end;
  transform: translate(10px, 8.6rem);
}
.cover p:nth-child(1):not(.cover span p) {
  width: 95%;
}
.cover p:nth-child(2) {
  font-size: 15px;
  transform: translate(10px, 8.7rem);
  height: 1.7rem;
  white-space: nowrap;
  overflow: hidden;
  width: 95%;
  text-overflow: ellipsis;
}
.cover span p{
  transform: translate(35px, 8.5rem);
  font-size: 12px;
}
.ticket-icon {
  position: absolute;
  bottom: 15px;
  left: .95rem;
  rotate: 135deg;
  font-size: 12px;
}
.bottompic {
  position: fixed;
  bottom: 20px;
}
.map {
  text-align: center;
  max-width: 97%;
  margin: 10px auto 0 auto;
}
.map img {
 border-radius: 10px;
}
.weirdh2 {
  background: #4c4c55;
  width: 97%;
  text-align: center;
  color: white;
  left: 1.4%;
  transform: translateY(50px);
  padding: 3px 0;
  z-index: 1;
}
.weirdh2:first-child {
font-size: 14px;
letter-spacing: .03em;
position: absolute;
}
.bg {
  background:rgb(128, 128, 128, .4);
  width: 97%;
  margin: 0 auto;
  left: 1.5%;
  transform: translateY(74px);
  z-index: 99999;
  filter: brightness(80%);
}
.sold {
  transform: translate(8.5rem, 4.9rem);
  width: 4rem;
  position: absolute;
  display: flex;
  justify-content: space-around;
}
.coverimg {
  filter: brightness(45%);
  object-fit: cover;
}
.qr-code-modal {
  position: fixed;
  top: 50%;
  left: 50%;
