  transform: translateY(-5.5rem);
}
marquee {
  background: linear-gradient(to right, rgb(255, 0, 0,.4), rgb(65, 65, 190), rgb(128, 0, 128,.5)); 
  color: #fff;
  height: 2rem;
  display: flex;
  align-items: center;
  font-size: 16px;
  font-style: italic;
  border-radius: 5px 5px 0 0;
