}
.qr-code-modal hr:nth-child(16) {
transform: translate(20px, -6rem);
width: 90%;
border-top: 2px dashed rgb(128, 128, 128, .7); 
border-bottom: none;
}
.qr-code-modal h4:nth-child(18) {
