/* Loading.css */

.loading-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2rem;
  width: 100%;
  transform: translate(0px,5.25rem);
  padding: 15px 0;
}

.loading-circle {
  position: relative;
  width: 100px;
  height: 100px;
  display: flex;
}

.loading-bar {
  position: absolute;
  width: 3.5px;
  height: 10px;
  background-color: rgb(128, 128, 128, .2);
  border-radius: 4px;
  transform-origin: 50% 140%;
  animation: backgroundAnimation 1s linear infinite;
  left: 50%;
}
.Sdetdiv .loading-bar {
  width: 4.5px;
  height: 13px;
  animation: SbackgroundAnimation 1s linear infinite;
}
@keyframes SbackgroundAnimation {
  0% {
    background-color: gray;
  } 
  100% {
    background-color: rgb(128, 128, 128, .2);
  }
}
.loading-bar:nth-child(1) {
  transform: rotate(0deg);
  animation-delay: 100ms; /* No delay for the first bar */
}

.loading-bar:nth-child(2) {
  transform: rotate(45deg);
  animation-delay: 200ms; /* 1s delay for the second bar */
}

.loading-bar:nth-child(3) {
  transform: rotate(90deg);
  animation-delay: 300ms; /* 2s delay for the third bar */
}

.loading-bar:nth-child(4) {
  transform: rotate(135deg);
  animation-delay: 400ms; /* 3s delay for the fourth bar */
}

.loading-bar:nth-child(5) {
  transform: rotate(180deg);
  animation-delay: 500ms; /* 4s delay for the fifth bar */
}

.loading-bar:nth-child(6) {
  transform: rotate(225deg);
  animation-delay: 600ms; /* 5s delay for the sixth bar */
}

.loading-bar:nth-child(7) {
  transform: rotate(270deg);
  animation-delay: 700ms; /* 6s delay for the seventh bar */
}

.loading-bar:nth-child(8) {
  transform: rotate(315deg);
  animation-delay: 800ms; /* 7s delay for the eighth bar */
}

@keyframes backgroundAnimation {
  0% {
    background-color: #026bde;
  } 
  100% {
    background-color: rgba(128, 128, 128, 0.1)
  };
}

